export class IHCBase {
    constructor(node) {
        this.Name = node.attributes["name"].value;
        var id = node.attributes["id"].value;
        this.Id = parseInt(id.substring(3), 16);
        this.NodeTagName = node.tagName;
    }
    FindAndAdd(node, subnodename, addnode) {
        var nodes = node.ownerDocument.evaluate(subnodename, node, null, XPathResult.ORDERED_NODE_ITERATOR_TYPE, null);
        do {
            var subnode = nodes.iterateNext();
            if (subnode == null)
                break;
            addnode(subnode);
        } while (true);
    }
}
export class IHCGroup extends IHCBase {
    constructor(node) {
        super(node);
        this.Note = node.getAttribute("note") || "";
        this.FunctionBlocks = new Array();
        this.Products = new Array();
        this.FindAndAdd(node, "functionblock", (subnode) => {
            this.FunctionBlocks.push(new IHCFunctionBlock(subnode));
        });
        var products = node.ownerDocument.evaluate("product_airlink", node, null, XPathResult.ORDERED_NODE_ITERATOR_TYPE, null);
        do {
            var pnode = products.iterateNext();
            if (pnode == null)
                break;
            var product = new IHCWirelessProduct(pnode);
            this.Products.push(product);
        } while (true);
        products = node.ownerDocument.evaluate("product_dataline", node, null, XPathResult.ORDERED_NODE_ITERATOR_TYPE, null);
        do {
            var pnode = products.iterateNext();
            if (pnode == null)
                break;
            var product = new IHCDatalineProduct(pnode);
            this.Products.push(product);
        } while (true);
    }
}
export class IHCProduct extends IHCBase {
    constructor(node) {
        super(node);
        this.Inputs = new Array();
        this.Outputs = new Array();
        this.Note = node.getAttribute("note") || "";
        this.Position = node.getAttribute("position") || "";
        this.FindAndAdd(node, "airlink_input", (subnode) => {
            this.Inputs.push(new IHCInput(subnode));
        });
        this.FindAndAdd(node, "airlink_dimmer_increase", (subnode) => {
            this.Inputs.push(new IHCInput(subnode));
        });
        this.FindAndAdd(node, "airlink_dimmer_decrease", (subnode) => {
            this.Inputs.push(new IHCInput(subnode));
        });
        this.FindAndAdd(node, "airlink_dimming", (subnode) => {
            this.Inputs.push(new IHCInput(subnode));
        });
        this.FindAndAdd(node, "dataline_input", (subnode) => {
            this.Inputs.push(new IHCInput(subnode));
        });
        this.FindAndAdd(node, "airlink_relay", (subnode) => {
            this.Outputs.push(new IHCOutput(subnode));
        });
        this.FindAndAdd(node, "dataline_output", (subnode) => {
            this.Outputs.push(new IHCOutput(subnode));
        });
        this.FindAndAdd(node, "light_indication", (subnode) => {
            this.Outputs.push(new IHCOutput(subnode));
        });
    }
}
export class IHCWirelessProduct extends IHCProduct {
    constructor(node) {
        super(node);
    }
}
export class IHCDatalineProduct extends IHCProduct {
    constructor(node) {
        super(node);
    }
}
export class IHCFunctionBlock extends IHCBase {
    constructor(node) {
        super(node);
        this.Note = node.getAttribute("note") || "";
        this.Inputs = new Array();
        var inputs = node.ownerDocument.evaluate("inputs/*", node, null, XPathResult.ORDERED_NODE_ITERATOR_TYPE, null);
        do {
            var inputnode = inputs.iterateNext();
            if (inputnode == null)
                break;
            var input = new IHCInput(inputnode);
            this.Inputs.push(input);
        } while (true);
        this.Outputs = new Array();
        var outputs = node.ownerDocument.evaluate("outputs/*", node, null, XPathResult.ORDERED_NODE_ITERATOR_TYPE, null);
        do {
            var outputnode = outputs.iterateNext();
            if (outputnode == null)
                break;
            var output = new IHCOutput(outputnode);
            this.Outputs.push(output);
        } while (true);
    }
}
export class IHCResource extends IHCBase {
    constructor(node) {
        super(node);
    }
    get IsLightLevel() {
        return this.NodeTagName == "resource_light_level" || this.NodeTagName == "link_dimming";
    }
}
export class IHCInput extends IHCResource {
    constructor(node) {
        super(node);
    }
}
export class IHCOutput extends IHCResource {
    constructor(node) {
        super(node);
    }
}
export class IHCProject {
    constructor(xmldoc) {
        this.xmldoc = xmldoc;
        this.Groups = new Array();
        var gs = this.xmldoc.evaluate("/utcs_project/groups/group", this.xmldoc, null, XPathResult.ORDERED_NODE_ITERATOR_TYPE, null);
        do {
            var node = gs.iterateNext();
            if (node == null)
                break;
            var group = new IHCGroup(node);
            this.Groups.push(group);
        } while (true);
    }
}
